@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=EB+Garamond:ital,wght@0,400;0,500;0,600;1,400;1,500;1,600&display=swap");
@mixin main-text {
  position: absolute;
  width: 100%;
  top: 0;
  text-align: center;
  height: 50%;
  top: 25%;
  letter-spacing: 8px;
  cursor: default;
}

$noise: url("data:image/png;base64,");
@mixin dithered-gradient($position, $start, $stop, $color) {
  background: radial-gradient(
    circle at $position,
    transparent $start,
    $color $stop
  );
  mask: $noise,
    radial-gradient(circle at $position, transparent $start, #000 ($stop + 10%));
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
html::-webkit-scrollbar {
  display: none;
}
body {
  background-color: #f1eee8;
  font-family: "PilcrowRounded-Heavy";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
nav {
  z-index: 50;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: auto;
  font-size: 2vmax;
  text-transform: capitalize;
  ul {
    list-style: none;
    display: flex;
    justify-content: space-around;
    li {
      line-height: 2;
      cursor: pointer;
      position: relative;
      --Width: 0;
      &::after {
        content: "";
        display: block;
        position: absolute;
        top: 42%;
        left: -5%;
        width: var(--Width);
        height: 36%;
        background-color: #e59500;
        opacity: 0.6;
        z-index: -1;
        transition: all 0.3s ease-in-out;
      }
    }
  }
}
.container {
  height: 100%;
  width: 100%;
  section {
    width: 100%;
  }
  .first {
    width: 100%;
    height: 40vmax;
    position: relative;
    .chair {
      mix-blend-mode: multiply;
      position: absolute;
      width: 40%;
      height: 40%;
      svg {
        height: 100%;
        width: 100%;
      }
      &.__one {
        left: 0;
        fill: #e88ea2;
      }
      &.__two {
        left: 30%;
        fill: #4a91d6;
      }
      &.__three {
        left: 40%;
        fill: #fc8c79;
        svg {
          height: 130%;
          width: 130%;
        }
      }
      &.__four {
        left: 60%;
        fill: #41a757;
        svg {
          height: 80%;
          width: 80%;
        }
      }
      &.__five {
        right: 0;
        fill: #e6c2df;
      }
      &.__six {
        fill: #add9e8;
        svg {
          height: 60%;
          width: 60%;
        }
      }
    }
    .text {
      z-index: 2;
      font-size: 7vmax;
      @include main-text;
    }
  }
  .second {
    width: 100%;
    height: 100vh;
    position: relative;
    #bg {
      width: 0;
      height: 0;
    }
    &Wrap {
      display: flex;
      align-items: flex-end;
      width: 100%;
      height: 100%;
      background-color: #ffe670;
      -webkit-clip-path: url(#mask);
      clip-path: url(#mask);
      .text {
        height: 80%;
        width: 40%;
        position: relative;
        .text__inner {
          display: flex;
          flex-direction: column;
          justify-content: center;
          font-size: 4.5vmax;
          height: 100%;
          width: 100%;
          position: absolute;
          span {
            font-family: "PilcrowRounded-Medium";
            margin: 0 auto;
            &:last-child {
              letter-spacing: 0.05em;
              margin: 5% 0 0 10%;
              font-size: 1.5vmax;
              color: white;
              -webkit-text-stroke: 1px black;
              cursor: pointer;
            }
          }
          p {
            overflow: hidden;
            font-style: italic;
            margin: 5% 0 0 10%;
            font-family: "Montserrat", sans-serif;
            font-size: 1vmax;
            letter-spacing: 0.03em;
            font-weight: 300;
          }
        }
      }
      .bigChairs {
        width: 60%;
        height: 90%;
        position: relative;
        .threeChair {
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          position: absolute;
        }
      }
    }
    .secondButton {
      display: flex;
      align-items: center;
      justify-content: space-between;
      position: absolute;
      top: 50%;
      right: 5%;
      transform: translateY(-50%);
      width: 50%;
      height: 10%;
      font-size: 5vmax;
      font-family: "EB Garamond", serif;
      div {
        cursor: pointer;
      }
    }
  }
  .third {
    height: 100%;
    width: 100%;
    display: grid;
    padding: 10vmax 5vmax;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(3, 35vmax);
    grid-gap: 10px;
    div {
      width: 100%;
      height: 100%;
      position: relative;
      cursor: pointer;
      &:not(#sofa__empty) {
        &:hover {
          &::after {
            color: #fff;
            display: flex;
            justify-content: center;
            align-items: center;
            text-decoration: underline;
            font-family: "PilcrowRounded-Medium";
            font-size: 2vmax;
            content: "See more detail";
            width: 100%;
            height: 100%;
            position: absolute;
            border-radius: inherit;
            top: 0;
            left: 0;
            backdrop-filter: blur(1.5px);
          }
        }
      }
    }
    #sofa__empty {
      background-image: radial-gradient(red 1px, transparent 1px);
      background-size: calc(10 * 1px) calc(10 * 1px);
      width: 100%;
      height: 100%;
      overflow: hidden;
      &:hover {
        background-image: radial-gradient(red 2.5px, transparent 2.5px);
      }
    }
    #sofa1 {
      background: url("https://images.unsplash.com/photo-1633114594701-52002208270f?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=2020&q=80")
        no-repeat;
      background-size: cover;
      background-position: center;
      border-top-left-radius: 10rem;
      border-top-right-radius: 10rem;
    }
    #sofa2 {
      background: url("https://images.unsplash.com/photo-1567016432779-094069958ea5?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=880&q=80")
        no-repeat;
      background-size: cover;
      background-position: left;
      border-top-left-radius: 50rem;
      border-bottom-left-radius: 50rem;
    }
    #sofa3 {
      grid-column: 3/5;
      background: url("https://images.unsplash.com/photo-1567016507665-356928ac6679?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=880&q=80")
        no-repeat;
      background-size: cover;
      background-position: right;
    }
    #sofa4 {
      background: url("https://images.unsplash.com/photo-1567016376408-0226e4d0c1ea?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=687&q=80")
        no-repeat;
      background-size: cover;
      background-position: center;
      border-top-right-radius: 10rem;
      border-bottom-left-radius: 10rem;
    }
    #sofa5 {
      grid-column: 1/4;
      background: url("https://images.unsplash.com/photo-1555041469-a586c61ea9bc?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1170&q=80")
        no-repeat;
      background-size: cover;
      background-position: top;
      border-radius: 50rem;
    }
    #sofa6 {
      background: url("https://images.unsplash.com/photo-1606744837616-56c9a5c6a6eb?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=735&q=80")
        no-repeat;
      background-size: cover;
      background-position: top;
      border-bottom-left-radius: 10rem;
      border-bottom-right-radius: 10rem;
    }

    #sofa7 {
      background: url("https://images.unsplash.com/photo-1551298698-66b830a4f11c?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=715&q=80")
        no-repeat;
      background-size: cover;
      background-position: top;
      border-radius: 10rem;
    }
    #sofa8 {
      grid-column: 3/6;
      background: url("https://images.unsplash.com/photo-1607646175036-fdba5063cfed?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1332&q=80")
        no-repeat;
      background-size: cover;
      background-position: top;
    }
  }

  @mixin circle-n($n, $color, $radius) {
    position: absolute;
    top: 0;
    left: -50%;
    width: 100%;
    height: 100%;
    @for $i from 1 through $n {
      div:nth-child(#{$i}) {
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        position: absolute;
        border: 1px solid $color;
        border-radius: $radius;
        height: calc(#{$i} * 7vmax);
        width: calc(#{$i} * 7vmax);
        opacity: 1;
        &:first-child {
          background: radial-gradient(
            circle at 100% 0,
            transparent 25%,
            #fffc51 65%
          );
          mask: url(https://davidjaldred.com/codepen/noise.svg),
            radial-gradient(circle at 100% 0, transparent 5%, #000 80%);
        }
      }
    }
  }

  @keyframes gravity {
    0% {
      transform: translateY(0);
    }
    50% {
      transform: translateY(30%);
    }
    100% {
      transform: translateY(0%);
    }
  }

  .fourth {
    width: 100%;
    height: 100%;
    font-size: 5vmax;
    font-family: "PilcrowRounded-Regular";
    &Wrap {
      width: 90%;
      padding: 5% 0;
      margin: auto;
      display: flex;
      justify-content: flex-end;
      position: relative;
      .circle {
        transform-origin: center;
        position: absolute;
        left: 0;
        width: 20%;
        height: 50%;
        bottom: 25%;
        &__chair {
          top: 0;
          left: 10%;
          position: absolute;
          width: 35%;
          height: 35%;
          fill: #e6c2df;
          &:nth-child(2) {
            top: 35%;
            left: 50%;
            animation-delay: 2s;
            fill: #e6ca51;
          }
          &:last-child {
            top: 70%;
            left: 5%;
            animation-delay: 2.5s;
            fill: #6b86df;
          }
        }
      }
      .textures {
        width: 30%;
        height: 40vmax;
        p {
          line-height: 1.5;
          position: relative;
          padding-left: 2vmax;
          overflow: hidden;
          --Opacity: 0;
          &::after {
            opacity: var(--Opacity);
            content: "\2192";
            position: absolute;
            transform: rotate(180deg);
            color: #ff4d3c;
          }
          &:hover {
            &::after {
              transform: rotate(0deg);
              animation: point 1s infinite;
            }
          }
        }
      }
      @keyframes point {
        0% {
          transform: translateX(0%);
        }
        50% {
          transform: translateX(30%);
        }
        100% {
          transform: translateX(0%);
        }
      }
      .pictures {
        height: 40vmax;
        width: 40%;
        position: relative;
        .picture {
          width: 0%;
          height: 100%;
          position: absolute;
          img {
            width: 100%;
            height: 100%;
            position: absolute;
            object-fit: contain;
          }
        }
      }
      .empty {
        z-index: -1;
        width: 50%;
        height: 100%;
      }
    }
  }
}

.loading {
  width: 100%;
  height: 100%;
  position: relative;
  &__circle {
    width: 10vmax;
    height: 10vmax;
    border-radius: 50%;
    background-color: #fff;
    opacity: 0.6;
    position: absolute;
    top: 35%;
    left: 40%;
    transform: translate(-50%, -50%);
    animation: double-bounce 2s infinite ease-in-out;
    &:last-child {
      animation-delay: -1s;
    }
  }
}

@keyframes double-bounce {
  0%,
  100% {
    transform: scale(0);
  }
  50% {
    transform: scale(1);
  }
}

.mouse {
  display: grid;
  place-items: center;
  align-items: end;
  position: absolute;
  width: 100%;
  height: 20%;
  font-size: 1.5vmax;
  animation: blink 2s linear infinite;
  svg {
    width: 100%;
    height: 30%;
    margin-bottom: 0.5%;
  }
}
@keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes arrow {
  0% {
    right: 2%;
  }
  50% {
    right: 4%;
  }
  100% {
    right: 2%;
  }
}
